import React,{useState} from 'react';

const Form = (props) => {
    const initialFormData = {
        author: "",
        name: "",
        image: "data:image/jpeg;base64,"
    };
    let [bookInfo, updateBookInfo] = useState(initialFormData);
    const handleChange = (e) => {
        updateBookInfo({
            ...bookInfo,
            [e.target.name]: e.target.value.trim()
        })
    };
    const encodeImage = async (e) => {
        const file = e.target.files[0]
        const base64= await props.convertBase64(file);
        updateBookInfo({
            ...bookInfo,
            image: base64
        })
    }


    const addBook = (e) =>{
        localStorage.setItem(`${bookInfo.author}${bookInfo.name}`,JSON.stringify(bookInfo));
        props.updateBookList(Object.keys(localStorage));
        e.clear();
        e.preventDefault()
    }

    return (
        <div className={"app__form"}>
        <form onSubmit={addBook}>
            <input className={"app__form_author"} type={"input"} name={"author"} placeholder={"Автор книги"} onChange={handleChange} required={true}/>
            <input className={"app__form_name"} type={"input"} name={"name"} placeholder={"Название книги"} onChange={handleChange} required={true}/>
            <input className={"app__form_image"} type={"file"} name={"image"} onChange={encodeImage}/>
            <button className={"app__form_submit"} type={"submit"}>Добавить книгу</button>
        </form>
        </div>
    )
}

export default Form;